.navigation {
  position: absolute;
  z-index: 1;
  display: flex;
  flex-direction: column;
  width: 100%;
  color: #000000;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .navigation {
    position: relative;
    z-index: 0;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 440px;
    padding-top: 20px;
  }
}
@media (min-width: 1440px) {
  .navigation {
    justify-content: flex-end;
    min-width: 40%;
    max-width: 570px;
    padding-top: 17px;
  }
}
@media (min-width: 1440px) {
  .header--main .navigation {
    background-color: transparent;
  }
}
.navigation__link {
  position: relative;
  padding-top: 22px;
  padding-bottom: 23px;
  padding-right: 20px;
  padding-left: 20px;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 20px;
  line-height: 20px;
  font-weight: 500;
  text-align: center;
  color: #000000;
  border-top: 1px solid #e6e6e6;
  text-decoration: none;
  text-transform: uppercase;
}
.navigation__link:hover {
  opacity: 0.6;
}
.navigation__link:focus {
  opacity: 0.4;
}
.navigation__link:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .navigation__link {
    padding-right: 0;
    padding-left: 0;
    margin-right: 33px;
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
    border: none;
  }
}
@media (min-width: 1440px) {
  .navigation__link {
    margin-right: 35px;
    font-size: 20px;
    line-height: 30px;
    color: #000000;
  }
}
@media (max-width: 767px) {
  .navigation__link:last-of-type {
    border-bottom: 1px solid #e6e6e6;
  }
}
@media (min-width: 768px) {
  .navigation__link:nth-of-type(3n) {
    margin-right: 0;
    margin-left: 4px;
  }
}
@media (min-width: 1440px) {
  .navigation__link:nth-of-type(3n) {
    margin-left: 0;
  }
}
@media (min-width: 1440px) {
  .header--main .navigation__link {
    color: #ffffff;
  }
}
@media (min-width: 768px) {
  .navigation__link--current::after {
    content: "";
    position: absolute;
    top: 55px;
    display: block;
    width: 100%;
    height: 2px;
    background-color: #68b738;
  }
}
@media (min-width: 1440px) {
  .navigation__link--current::after {
    top: 58px;
  }
}
@media (min-width: 1440px) {
  .header--main .navigation__link--current::after {
    background-color: #ffffff;
  }
}
.navigation__toggle {
  display: none;
}
@media (max-width: 767px) {
  .navigation__toggle {
    position: absolute;
    top: -59px;
    right: 8px;
    display: block;
    width: 52px;
    height: 48px;
    background-color: #ffffff;
    cursor: pointer;
    border: none;
  }
  .navigation__toggle:hover {
    opacity: 0.6;
  }
  .navigation__toggle:focus {
    opacity: 0.4;
  }
  .navigation__toggle:active {
    opacity: 0.3;
  }
  .navigation--opened .navigation__toggle::before,
  .navigation--opened .navigation__toggle::after {
    content: "";
    position: absolute;
    top: 22px;
    right: 12px;
    width: 24.8px;
    height: 1.5px;
    background-color: #000000;
  }
  .navigation--opened .navigation__toggle::before {
    transform: rotate(45deg);
  }
  .navigation--opened .navigation__toggle::after {
    transform: rotate(-45deg);
  }
  .navigation--closed .navigation__toggle::before {
    content: "";
    position: absolute;
    top: 16px;
    right: 12px;
    width: 24px;
    height: 2px;
    background-color: #000000;
    box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
  }
  .navigation--closed .navigation__link {
    display: none;
  }
}
@media (max-width: 767px) and (min-width: 768px) {
  .navigation__toggle {
    display: none;
  }
}
.navigation--nojs .navigation__toggle {
  display: none;
}
.navigation--nojs {
  position: static;
}
